
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Card from 'vux/src/components/card/index.vue'

import Tag from 'c%/Tag.vue'

export default{
mixins: [ common, globals ],
components: {
Tag,
Card
},
data () {
return {
data: [],
map_lesson_type: {0:'班',1:'一',2:'多'}
}
},
methods: {
detail (item) {
this.$router.push({path:'./prepare/'+item.cp_id,params:{id:item.cp_id}})
}
},
computed: {
data_uri () {
return 'course_prepares?sid='+ this.sid$
},
style () {
let top = this.headerHeight
return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
}
}
}




